import { useState, useContext, useEffect } from 'react';
import { useNavigate } from 'react-router-dom';
import { Context } from '../../../../..';
import { $ } from '../../../../../components/customeJQ/customeJQ';
import { ReactComponent as Loupe } from '../../../../../img/icons/loupe.svg';
import { change, focus } from './focus';
import './SearchBlock.scss';

export default function SearchBlock() {
  const [value, setValue] = useState('');
  const { device } = useContext(Context);
  const onChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    setValue(e.target.value);
    change(e);
    if (e.target.value) {
      $('.searchblock__items').add('add');
    } else {
      $('.searchblock__items').remove('add');
    }
  };
  const router = useNavigate();
  const [count, setCount] = useState(-1);

  const handlerKey = (e: React.KeyboardEvent) => {
    const arr = Array.from(
      document.querySelectorAll('button.searchblock__item')
    ) as HTMLButtonElement[];
    if (
      (
        document.querySelector('.searchblock__items') as HTMLElement
      )?.classList.contains('add')
    ) {
      let number = count;
      if (e.key == 'ArrowDown') {
        e.preventDefault();
        if (number != 2) number += 1;
        arr[number]?.focus();
      } else if (e.key == 'ArrowUp') {
        e.preventDefault();
        if (number != 0) number -= 1;
        arr[number]?.focus();
      } else if (e.key == 'Backspace') {
        (
          document.querySelector(
            '.searchblock__input input'
          ) as HTMLInputElement
        )?.focus();
      }
      setCount(number);
    }
  };

  const handlerClickItem = (id) => {
    router(`/shop/item/${id}`);
    document.documentElement.scrollIntoView(true);
  };
  return (
    <div className="searchblock">
      <div className="searchblock__input">
        <div className="searchblock__text old all">
          <p className="mini">Search</p>
        </div>
        <input
          onKeyDown={handlerKey}
          onFocus={(e) => focus(e, setCount)}
          onBlur={(e) => focus(e, setCount)}
          type="text"
          value={value}
          onChange={onChange}
        />
      </div>
      <div
        onClick={() => $('.searchblock__input').toggle('add')}
        className="searchblock__loupe"
      >
        <Loupe />
      </div>
      <div className="searchblock__items" onKeyDown={handlerKey}>
        {device.getDeviceAll.map((item) => {
          if (value != '') {
            if (
              item.name.toUpperCase().startsWith(value.toUpperCase()) ||
              item.brand.name.toUpperCase().startsWith(value.toUpperCase())
            ) {
              return (
                <button
                  onClick={() => handlerClickItem(item.id)}
                  key={item.id}
                  className="searchblock__item"
                >
                  <p className="mini">{item.name}</p>
                </button>
              );
            }
          }
        })}
      </div>
    </div>
  );
}
