import { $ } from '../../../../../components/customeJQ/customeJQ';

export const change = (e: React.ChangeEvent<HTMLInputElement>) => {
  if (e.target?.value) {
    e.target
      .closest('.searchblock__input')
      ?.querySelector('.searchblock__text')
      ?.classList.remove('old');
  } else {
    e.target
      .closest('.searchblock__input')
      ?.querySelector('.searchblock__text')
      ?.classList.add('old');
  }
};

export const focus = (
  e: React.FocusEvent<HTMLInputElement>,
  setCount: React.Dispatch<React.SetStateAction<number>>
) => {
  e.target.removeAttribute('readOnly');
  const target = e.target
    ?.closest('.searchblock__input')
    ?.querySelector('.searchblock__text');

  if (e.type != 'blur') {
    target?.classList.add('focus');
    if (e.target?.value != '') $('.searchblock__items').add('add');
    setCount(-1);
  } else {
    //  $('.searchblock__items').remove('add');
    if (e.target?.value == '') {
      target?.classList.remove('focus');
      target?.classList.add('old');
    }
  }
};
