import './style/SortBlock.scss';
import { observer } from 'mobx-react-lite';
import { useContext, useEffect, useState } from 'react';
import { Context } from '../../../../..';
import { $ } from '../../../../../components/customeJQ/customeJQ';
import { useSearchParams } from 'react-router-dom';

export const SortPrice: React.FC = observer(() => {
  const [low, setLow] = useState('0');
  const [tall, setTall] = useState('200');
  const [type, setType] = useState('');
  const { device } = useContext(Context);
  const [searchParams, setSearchParams] = useSearchParams();

  useEffect(() => {
    searchParams.set('low', low);
    searchParams.set('tall', tall);
    setSearchParams(searchParams);

    device.setPage = '1';
    device.setSale = !device.getSale;
  }, [low, tall]);

  const change = () => {
    let max = 0;
    if (device.getDevice.length > 0) {
      device.getDevice.map((item) => {
        if (+item.price > max) {
          max = +item.price;
        }
      });
      setTall(max.toString());
      $('.sortprice__thumbtall').take().style.left =
        max.toString().replace(/[^\d]*/g, '') + 'px';
    }
  };

  useEffect(() => {
    if (device.getOnType?.name && device.getOnType?.name != type) {
      change();
      setType(device.getOnType.name);
    }
  }, [device.getDevice]);

  useEffect(() => {
    const thumblow = $('.sortprice__thumblow').take() as HTMLElement;
    const thumtall = $('.sortprice__thumbtall').take() as HTMLElement;

    thumblow.addEventListener('mousedown', handlerSlider);
    thumtall.addEventListener('mousedown', handlerSlider);
    return () => {
      thumblow.removeEventListener('mousedown', handlerSlider);
      thumtall.removeEventListener('mousedown', handlerSlider);
    };
  }, []);

  const handlerSlider = (e) => {
    if (e.target.closest('.sortprice__thumblow')) {
      thumbchange(e, setLow, setTall);
    } else {
      thumbchange(e, setLow, setTall);
    }
  };

  const handlerChange = (e) => {
    setLow(e.target.value.replace(/[^\d]*/g, ''));
    const target = $(e.target.closest('input')).take() as HTMLElement;
    console.log(target.dataset.low);

    if (target.dataset.low) {
      $('.sortprice__thumblow').take().style.left =
        e.target.value.replace(/[^\d]*/g, '') + 'px';
    } else {
      $('.sortprice__thumbtall').take().style.left =
        e.target.value.replace(/[^\d]*/g, '') + 'px';
    }
  };

  return (
    <li data-type="numberLager " className="sortprice">
      <div className="sortprice__price">
        <div className="sortprice__low sortprice__input">
          <input
            type="text"
            value={low}
            onChange={handlerChange}
            maxLength={4}
          />
        </div>

        <div className="sortprice__tall sortprice__input">
          <input
            type="text"
            value={tall}
            onChange={handlerChange}
            maxLength={4}
          />
        </div>
      </div>
      <div className="sortprice__line">
        <div className="sortprice__lineblock">
          <span className="sortprice__thumblow sortprice__thumb"></span>
          <span className="sortprice__slider"></span>
          <span className="sortprice__thumbtall sortprice__thumb"></span>
        </div>
      </div>
    </li>
  );
});

function thumbchange(e, setLow, setTall) {
  e.preventDefault();
  const thumb = $(
    e.target.closest('.sortprice__thumb') as HTMLElement
  ).take() as HTMLElement;
  const slider = $('.sortprice__slider').take() as HTMLElement;
  const shiftX = e.clientX - thumb.getBoundingClientRect().left;

  document.addEventListener('mousemove', move);
  document.addEventListener('mouseup', handlerupmous);

  function move(e) {
    let left = e.clientX - shiftX - slider.getBoundingClientRect().left;

    if (left < 0) {
      left = 0;
    }

    const right = slider.offsetWidth - thumb.offsetWidth;

    if (left > right) {
      left = right;
    }

    if (thumb.classList.contains('sortprice__thumblow')) {
      if (
        left < +$('.sortprice__thumbtall').take().style.left.replace('px', '')
      ) {
        setLow(+left.toString().replace(/\.\d*/g, ''));
        thumb.style.left = left + 'px';
      }
    } else {
      if (
        left > +$('.sortprice__thumblow').take().style.left.replace('px', '')
      ) {
        setTall(+left.toString().replace(/\.\d*/g, ''));
        thumb.style.left = left + 'px';
      }
    }
  }

  function handlerupmous(e) {
    document.removeEventListener('mousemove', move);
    document.removeEventListener('mouseup', handlerupmous);
  }
}
