import { useEffect } from 'react';
import { addClassMenu } from '../../../../../components/burger/burger';
import { $ } from '../../../../../components/customeJQ/customeJQ';
import './style/Mune.scss';

const Menu: React.FC<MenuType> = ({ name }) => {
  useEffect(() => {
    window.addEventListener('click', addClassMenu);
    return () => window.removeEventListener('click', addClassMenu);
  }, []);

  return (
    <div className={`shop__menu ${name}`}>
      <div className="shop__body"></div>
      <div
        className="shop__btn"
        onClick={() => {
          $('.shop__menu').remove('show');
        }}
      >
        <p className="">Continue</p>
      </div>
    </div>
  );
};

type MenuType = {
  name?: string;
};

export default Menu;
