import { Goods } from './Goods/Goods';
import Menu from './Tuner/components/Menu/Menu';
import { useEffect } from 'react';

export default function Shop() {
  useEffect(() => {
    document.title = `Shop`;
  }, []);
  return (
    <>
      <Menu name="shop" />
      <div className="indentstart"></div>
      <Goods />
    </>
  );
}
