import { observer } from 'mobx-react-lite';
import React, { useEffect, useState, useContext } from 'react';
import { Context } from '../../..';
import { renderSortMenu } from '../../../components/Product/addClassSort&Search';
import useLoading from '../../../Hooks/useLoading';
import { useFetching } from '../../../http/shopAPI';
import GoodsRow from '../../../UI/GoodsRow/GoodsRow';
import Loading from '../../../UI/Loading/Loading';
import ProductTable from './components/ProductTable/ProductTable';
import { Tuner } from '../Tuner/Tuner';
import './style/Goods.scss';
import axios from 'axios';
import { useLocation, useSearchParams } from 'react-router-dom';
import {
  handlerSort,
  SortProduct,
} from '../Tuner/components/SortBlock/SortProduct';
import { $ } from '../../../components/customeJQ/customeJQ';
import { AsideBlockTurn } from '../Tuner/AsideBlockTurn';
import { LoadingWin } from '../../../UI/WinLoad/WinLoad';

export const Goods: React.FC = observer(() => {
  const { user, device, tuner } = useContext(Context);
  const params = useLocation().search;
  const [limit, setLimit] = useState('8');
  const [isLoading, fetching] = useLoading(async () => {
    Promise.all([
      useFetching.getBrands(),
      useFetching.getTypes(),
      useFetching.getAll(),
      useFetching.getColor(),
    ]).then(
      axios.spread((brands, types, goods, colors) => {
        device.setBrands = brands;
        device.setTypes = types;
        device.setDeviceAll = goods.rows;
        tuner.setColors = colors;
      })
    );
  });
  const [searchParams, setSearchParams] = useSearchParams();

  const [isLoadingTwo, fetchingGood] = useLoading(async () => {
    await useFetching
      .getAllBrandOrType(device.getOnType?.id, searchParams)
      .then((resolve) => {
        device.setDevice(resolve.rows, limit);
        device.setPages =
          resolve.count > limit
            ? Math.ceil(+resolve.count / +limit).toString()
            : '0';
      });

    filters();
  });

  const filters = () => {
    const target = ($('.filter  ul li.add').take() as HTMLElement)?.dataset
      .type;
    if (target) {
      const arr = device.getDeviceAllBrand;
      handlerSort(target, arr);
      device.setDevice(arr, limit);
    }
  };

  const changeLimit = () => {
    const width = window.innerWidth;
    if (width <= 1420) {
      setLimit('6');
    } else {
      setLimit('8');
    }
  };

  useEffect(() => {
    device.changePage(limit);
  }, [device.getPage, limit]);

  useEffect(() => {
    fetchingGood();
  }, [
    user.getAuth,
    device.getOnType,
    device.getSale,
    device.getOnBrand,
    tuner.getOnColor,
  ]);

  useEffect(() => {
    window.addEventListener('resize', changeLimit);
    window.addEventListener('click', renderSortMenu);
    return function cleanup() {
      window.removeEventListener('resize', changeLimit);
      window.removeEventListener('click', renderSortMenu);
    };
  }, []);

  useEffect(() => {
    fetching();
    if (searchParams.has('page')) {
      device.setPage = searchParams.get('page')?.toString() || '1';
    } else if (device.getTypes.length > 0) {
      device.setPage = device.getTypes[0].id.toString();
    } else {
      device.setPage = '1';
    }

    if (+device.getPages > 1) {
      searchParams.set('page', device.getPage);
      setSearchParams(searchParams);
    }
  }, []);

  useEffect(() => {
    if (params == '' && device.getTypes.length > 0) {
      device.setOnType = device.getTypes[0];
      searchParams.set('type', device.getTypes[0].id.toString());

      setSearchParams(searchParams);
      fetchingGood();

      device.setPage = '1';
    }
  }, [params]);

  return (
    <div className="goods indent_last">
      <>
        <Tuner />
        <GoodsRow>
          <AsideBlockTurn>
            <SortProduct limit={limit} />
          </AsideBlockTurn>
          <LoadingWin isLoading={isLoading}>
            <ProductTable />
          </LoadingWin>
        </GoodsRow>
      </>
    </div>
  );
});
