import { observer } from 'mobx-react-lite';
import { useContext, useMemo, useEffect, useState } from 'react';
import { useLocation, useSearchParams } from 'react-router-dom';
import { Context } from '../../..';
import { Brends, useNavigateSearch } from './components/Brends/Brends';
import { ProductCategoryRow } from './components/ProductCategoryRow/ProductCategoryRow';
import { ReactComponent as X } from '../../../img/icons/x.svg';
import WEmersion from './components/WEmersion/WEmersion';
import { TypesConfig } from '../../../mobx/GlobalState.config';
import './style/Tuner.scss';
import { Colors } from './components/Color/Color';
import { $ } from '../../../components/customeJQ/customeJQ';
import { SortPrice } from './components/SortBlock/SortPrice';

export const AsideBlockTurn: React.FC<any> = observer(({ children }) => {
  const { device } = useContext(Context);

  return (
    <div className="sort__aside">
      <div className="sort__wrap">
        <div className="sort__blockone">
          <div className="sort__block filter">
            <div className="sort__title">
              <h3>Filter</h3>
            </div>
            <WEmersion name="All">{children}</WEmersion>
          </div>

          <div className="sort__block">
            <div className="sort__title">
              <h3>Category</h3>
            </div>
            <WEmersion name={device.getOnType?.name || 'All'}>
              <ProductCategoryRow />
            </WEmersion>
          </div>
          <div className="sort__block">
            <div className="sort__title">
              <h3>Brend</h3>
            </div>
            <WEmersion name={'All'}>
              <Brends />
            </WEmersion>
          </div>
          <div className="sort__block">
            <div className="sort__title">
              <h3>Color</h3>
            </div>
            <WEmersion name={'All'}>
              <Colors />
            </WEmersion>
          </div>
          <div className="sort__block">
            <div className="sort__title">
              <h3>Price</h3>
            </div>
            <WEmersion name={'Price range'}>
              <SortPrice />
            </WEmersion>
          </div>
          <div className="sort__block">
            <SaleAsideBlock />
          </div>
        </div>
      </div>
      <TurnBlockAddition />
    </div>
  );
});

export const SaleAsideBlock: React.FC = observer(() => {
  const [searchParams, setSearchParams] = useSearchParams();
  const { device } = useContext(Context);

  const selected = (e: React.MouseEvent<HTMLElement>) => {
    const target = e.target as HTMLElement;

    $(target.closest('li') as HTMLElement).toggle('add');
    if (searchParams.toString() != '') {
      if (!searchParams.has(`sale`)) {
        searchParams.set('sale', 'true');
      } else {
        searchParams.delete(`sale`);
      }
      setSearchParams(searchParams);
    } else {
      searchParams.set('sale', 'true');
      setSearchParams(searchParams);
    }
    device.setPage = '1';
    device.setSale = !device.getSale;
  };

  return (
    <div className="saleasideblock">
      <ul>
        <li
          className={searchParams.get('sale') ? 'add' : ''}
          onClick={(e) => selected(e)}
        >
          <div className="wemersion__ok"></div>
          <div className="wemersion__text">
            <p className="mini">Discount goods</p>
          </div>
        </li>
      </ul>
    </div>
  );
});

export const TurnBlockAddition: React.FC = observer(() => {
  const { device, tuner } = useContext(Context);
  const query = useQuery();
  const [arr, setArr] = useState<TypesConfig[]>([]);
  const params = useNavigateSearch();

  const handlerDelete = (item: TypesConfig) => {
    const items: TypesConfig[] = [];
    const color: TypesConfig[] = [];
    params(item.dop, item);
    for (const [key, value] of query.entries()) {
      if (key.startsWith('brand')) {
        device.getBrands.map((item) => {
          if (item.id == +value) items.push(item);
        });
      } else if (key.startsWith('color')) {
        tuner.getColors.map((item) => {
          item['dop'] = 'color';
          if (item.id == +value) color.push(item);
        });
      }
    }
    device.setOnBrand = items;
    tuner.setOnColor = color;
  };

  const isTrust = () => {
    const items: TypesConfig[] = [];
    if (device.getBrands) {
      for (const [key, value] of query.entries()) {
        if (key.startsWith('brand')) {
          device.getBrands.map((item) => {
            item['dop'] = 'brand';
            if (item.id == +value) items.push(item);
          });
        } else if (key.startsWith('color')) {
          tuner.getColors.map((item) => {
            item['dop'] = 'color';
            if (item.id == +value) items.push(item);
          });
        }
      }
    }
    setArr(items);
  };

  useEffect(() => {
    isTrust();
  }, [device.getOnBrand, tuner.getOnColor, device.getBrands]);

  return (
    <div className="sort__add">
      <div className="sort__additems">
        {arr.length > 0 &&
          arr.map((item: TypesConfig) => {
            return (
              <div key={item.id + '' + item.name} className="sort__item">
                <p className="mini ">
                  {item.name
                    .split('')
                    .map((item, index) => {
                      if (index == 0) return item.toUpperCase();
                      return item;
                    })
                    .join('')}
                </p>
                <div
                  onClick={() => handlerDelete(item)}
                  className="sort__close"
                >
                  <X />
                </div>
              </div>
            );
          })}
      </div>
    </div>
  );
});

export function useQuery() {
  const { search } = useLocation();

  return useMemo(() => new URLSearchParams(search), [search]);
}
