import { TypeHarmonic } from '../../../../../interface/ui.interface';
import './style/WEmersion.scss';

const WEmersion: React.FC<TypeHarmonic> = ({ name, children }) => {
  return (
    <div data-filter className={`${name} wemersion`}>
      <div className="wemersion__head">
        <p className="mini">{name}</p>
        <p className="wemersion__arrow mini">{'>'}</p>
      </div>
      <div className="wemersion__blockharmonic">
        <ul className="wemersion__blockemersion">{children}</ul>
      </div>
    </div>
  );
};

export default WEmersion;
