import { Context } from '../../../../../index';
import { observer } from 'mobx-react-lite';
import { useContext } from 'react';
import { selectedMine } from '../selectedMine';
import './style/SortBlock.scss';

type SortConf = {
  limit: string;
};

export const SortProduct: React.FC<SortConf> = observer(({ limit }) => {
  const { device } = useContext(Context);

  const checkSorting = (e: React.MouseEvent<HTMLElement>) => {
    const target = ((e.target as HTMLElement).closest('li') as HTMLElement)
      .dataset.type;
    selectedMine(e, 'li');
    let arr = device.getDeviceAllBrand;
    arr = handlerSort(target, arr);
    device.setDevice(arr, limit);
  };

  return (
    <>
      <li data-type="number" onClick={checkSorting}>
        <div className="wemersion__ok"></div>
        <div className="wemersion__text">
          <p className="mini">Price: Low - High</p>
        </div>
      </li>
      <li data-type="numberLager" onClick={checkSorting}>
        <div className="wemersion__ok"></div>
        <div className="wemersion__text">
          <p className="mini">Price: High - Low</p>
        </div>
      </li>
    </>
  );
});

export const handlerSort = (target: string | undefined, arr: any) => {
  if (target == 'number') {
    arr.sort((a, b) => +a.price.replace('$', '') - +b.price.replace('$', ''));
  } else if (target == 'numberLager') {
    arr.sort((a, b) => +b.price.replace('$', '') - +a.price.replace('$', ''));
  }
  return arr;
};
