import { ItemProduct } from '../ItemProduct/ItemProduct';
import React, { useContext } from 'react';
import { observer } from 'mobx-react-lite';
import { Context } from '../../../../..';
import { DeviceConfigAll } from '../../../../../mobx/GlobalState.config';
import { Pagination } from '../Pagination/Pagination';
import '../../style/Goods.scss';
import { useSearchParams } from 'react-router-dom';

type ProductTypes = {
  like?: boolean;
};

const ProductTable = observer(({ like }: ProductTypes): JSX.Element => {
  const { device, favorite } = useContext(Context);
  const [searchParams] = useSearchParams();

  const blockProd = () => {
    const rows: JSX.Element[] = [];
    let products;
    if (like) {
      products = favorite.getProductsLimit;
    } else {
      if (searchParams.has('sale')) {
        products = device.getDevice.filter((item) => item.sale > 0);
      } else {
        products = device.getDevice;
      }
    }

    products.map((product: DeviceConfigAll) => {
      rows.push(<ItemProduct product={product} key={product.name} />);
    });
    return rows;
  };

  return (
    <div className="product__table">
      <div className="product__goods">{blockProd()}</div>
      {+device.getPages > 1 && !like && <Pagination />}
    </div>
  );
});

export default ProductTable;
