import axios from 'axios';
import { observer } from 'mobx-react-lite';
import React, { useContext, useEffect } from 'react';
import { useSearchParams } from 'react-router-dom';
import { Context } from '../../../../..';
import { useFetching } from '../../../../../http/shopAPI';
import { TypesConfig } from '../../../../../mobx/GlobalState.config';
import { selectedMine } from '../selectedMine';

export const ProductCategoryRow = observer(() => {
  const { device, tuner } = useContext(Context);
  const [searchParams, setSearchParams] = useSearchParams();
  const selected = (e: React.MouseEvent<HTMLElement>, item: TypesConfig) => {
    if (!(+(searchParams.get('type') as string) == +item.id)) {
      selectedMine(e, 'li');
      device.setOnType = item;
      device.setOnBrand = [{ id: 0, name: 'ALL' }];
      tuner.setOnColor = [{ id: 0, name: 'ALL' }];
      device.setPage = '1';
      setSearchParams({ type: `${item.id}` });
    }
  };

  useEffect(() => {
    let typeId: string;
    Promise.all([useFetching.getTypes()])
      .then(
        axios.spread((types) => {
          device.setTypes = types;
          if (searchParams.has('type')) {
            types.filter((item) => {
              if (+item.id == +(searchParams.get('type') as string)) {
                device.setOnType = item;
              }
            });
          } else {
            device.setOnType = types[0];
            typeId = types[0].id;
          }
        })
      )
      .then(() => {
        if (typeId) setSearchParams({ type: typeId });
      });
  }, []);

  const sort = () => {
    const map = new Map();
    const types = device.getTypes;

    if (Array.isArray(types)) {
      types.map((item, index) => {
        map.set(
          item.id,
          <li
            data-type={item.name}
            className={device.getOnType?.id == item.id ? 'add' : ''}
            onClick={(e) => selected(e, item)}
            key={index}
          >
            <div className="wemersion__ok"></div>
            <div className="wemersion__text">
              <p className="mini">
                {item.name.split('').map((item2: string, index2: number) => {
                  return index2 == 0
                    ? item2.toUpperCase()
                    : item2.toLowerCase();
                })}
              </p>
            </div>
          </li>
        );
      });
    }
    return Array.from(map.values());
  };

  return <>{sort()}</>;
});
