import { observer } from 'mobx-react-lite';
import { useContext, useEffect } from 'react';
import { Context } from '../../..';
import { addClassMenu, showMenu } from '../../../components/burger/burger';
import { ReactComponent as Indent } from '../../../img/icons/indent.svg';
import SearchBlock from './components/SearchBlock/SearchBlock';
import './style/Tuner.scss';

export const Tuner: React.FC = observer(() => {
  const { device } = useContext(Context);
  useEffect(() => {
    showMenu();
  }, []);

  return (
    <div className="container">
      <div className="product__tuner tuner">
        <div className="tuner__container">
          <div className="tuner__nameCategory">
            <h2>{device.getOnType?.name}</h2>
          </div>
          <BlockTuner />
        </div>
      </div>
    </div>
  );
});

export const BlockTuner: React.FC = observer(() => {
  return (
    <div className="tuner__sort sort">
      <div className="sort__search">
        <SearchBlock />
      </div>

      <div onClick={addClassMenu} className="sort__filter">
        <Indent />
      </div>
    </div>
  );
});
