import { observer } from 'mobx-react-lite';
import React, { useContext } from 'react';
import { useSearchParams } from 'react-router-dom';
import { Context } from '../../../../..';
import {
  DeviceConfigAll,
  TypesConfig,
} from '../../../../../mobx/GlobalState.config';
import { useNavigateSearch } from '../Brends/Brends';
import { selectedMineAll } from '../selectedMine';

export const Colors = observer(() => {
  const [searchParams] = useSearchParams();
  const { device, tuner } = useContext(Context);
  const params = useNavigateSearch();
  const selected = (e: React.MouseEvent<HTMLElement>, item: TypesConfig) => {
    selectedMineAll(e, 'li');
    let color: TypesConfig[] = tuner.getOnColor.reduce(
      (prev: TypesConfig[], item) => {
        prev.push(item);
        return prev;
      },
      []
    );

    if (
      color.filter((el) => {
        if (el.id == item.id) {
          return item;
        }
      }).length == 0
    ) {
      color.push(item);
    } else {
      color = color.filter((el) => {
        if (el.id != item.id) {
          return el;
        }
      });
    }
    tuner.setOnColor = color;
    device.setPage = '1';

    params('color', item);
  };

  const sort = () => {
    const map = new Map();
    const colors = tuner.getColors;

    map.set(
      'All',
      <li
        key="All"
        className={searchParams.toString().includes('color') ? '' : 'add'}
        data-type="All"
        onClick={(e) => selected(e, { id: 0, name: 'ALL' })}
      >
        <div className="wemersion__ok"></div>
        <div className="wemersion__text">
          <p className="mini">All Colors</p>
        </div>
      </li>
    );
    if (Array.isArray(colors)) {
      !!device.getOnType &&
        colors.map((item: TypesConfig, index) => {
          if (item.name != 'colorless') {
            if (
              device.getDeviceAll.filter(
                (el: DeviceConfigAll) =>
                  +el.typeId == device.getOnType.id && +el.colorId == item.id
              ).length != 0
            ) {
              map.set(
                item.id,
                <li
                  className={
                    searchParams.get('color' + item.id) == item.id.toString()
                      ? 'add'
                      : ''
                  }
                  data-type={item.name}
                  onClick={(e) => selected(e, item)}
                  key={index}
                >
                  <div className="wemersion__ok"></div>
                  <div className="wemersion__text">
                    <p className="mini">
                      {item.name
                        .split('')
                        .map((item2: string, index2: number) => {
                          return index2 == 0
                            ? item2.toUpperCase()
                            : item2.toLowerCase();
                        })}
                    </p>
                  </div>
                  <div
                    className="wemersion__color"
                    style={{ background: `${item.color}` }}
                  ></div>
                </li>
              );
            }
          }
        });
    }
    return Array.from(map.values());
  };

  return <>{sort()}</>;
});
