import { observer } from 'mobx-react-lite';
import React, { useContext } from 'react';
import { useSearchParams } from 'react-router-dom';
import { Context } from '../../../../..';
import {
  DeviceConfigAll,
  TypesConfig,
} from '../../../../../mobx/GlobalState.config';
import { selectedMineAll } from '../selectedMine';

export const Brends = observer(() => {
  const [searchParams] = useSearchParams();
  const { device } = useContext(Context);
  const params = useNavigateSearch();
  const selected = (e: React.MouseEvent<HTMLElement>, item: TypesConfig) => {
    selectedMineAll(e, 'li');
    let brand: TypesConfig[] = device.getOnBrand.reduce(
      (prev: TypesConfig[], item) => {
        prev.push(item);
        return prev;
      },
      []
    );
    brand = filter(brand, item);
    device.setOnBrand = brand;
    device.setPage = '1';

    params('brand', item);
  };

  const sort = () => {
    const map = new Map();
    const brands = device.getBrands;

    map.set(
      'All',
      <li
        key="All"
        className={searchParams.toString().includes('brand') ? '' : 'add'}
        data-type="All"
        onClick={(e) => selected(e, { id: 0, name: 'ALL' })}
      >
        <div className="wemersion__ok"></div>
        <div className="wemersion__text">
          <p className="mini">All Brends</p>
        </div>
      </li>
    );
    if (Array.isArray(brands)) {
      !!device.getOnType &&
        brands.map((item: TypesConfig, index) => {
          if (
            device.getDeviceAll.filter(
              (el: DeviceConfigAll) =>
                +el.typeId == device.getOnType.id && +el.brandId == item.id
            ).length != 0
          ) {
            map.set(
              item.id,
              <li
                className={
                  searchParams.get('brand' + item.id) == item.id.toString()
                    ? 'add'
                    : ''
                }
                data-type={item.name}
                onClick={(e) => selected(e, item)}
                key={index}
              >
                <div className="wemersion__ok"></div>
                <div className="wemersion__text">
                  <p className="mini">
                    {item.name
                      .split('')
                      .map((item2: string, index2: number) => {
                        return index2 == 0
                          ? item2.toUpperCase()
                          : item2.toLowerCase();
                      })}
                  </p>
                </div>
              </li>
            );
          }
        });
    }
    return Array.from(map.values());
  };

  return <>{sort()}</>;
});

export const useNavigateSearch = () => {
  const [searchParams, setSearchParams] = useSearchParams();
  return (name, item) => {
    if (item.id != 0) {
      if (searchParams.toString() != '') {
        if (!searchParams.has(`${name}${item.id}`)) {
          searchParams.set(name + '' + item.id, `${item.id}`);
        } else {
          searchParams.delete(`${name}${item.id}`);
        }
        setSearchParams(searchParams);
      } else {
        searchParams.set(name + '' + item.id, `${item.id}`);
        setSearchParams(searchParams);
      }
    } else {
      const obj = {};
      for (const [key, value] of searchParams.entries()) {
        if (!key.startsWith(name)) {
          obj[key] = value;
        }
      }
      setSearchParams(obj);
    }
  };
};

export function filter(block, item) {
  if (
    block.filter((el) => {
      if (el.id == block.id) {
        return item;
      }
    }).length == 0
  ) {
    block.push(item);
  } else {
    block = block.filter((el) => {
      if (el.id != item.id) {
        return el;
      }
    });
  }
  return block;
}
